.item-group {
    transition: transform 0.2s;
    /* Animation */
}

.item-group:hover {
    color: #0070f3;
    transform: scale(1.1);
}

.name-group {
    font-style: center;
    text-align: center;
}

.create-group {
    display: flex;
    justify-content: end;
}

.form-create {
    background: #154c79 !important;
    min-height: 100vh;
    display: flex;
    font-weight: 400;
    font-family: "Fira Sans", sans-serif;
}
.form-create .container{
    margin-top:40px;
}
h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
    font-weight: 500;
    font-family: "Fira Sans", sans-serif;
}
.error{
    color:red ;
}
.form-create{
    min-height: 100vh !important;
}